import React from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { Layout, Container } from 'layouts';
import config from '../../config/site';
import { graphql } from 'gatsby';
import './customsyle.css';
import Prism from 'prismjs';
require('prismjs/themes/prism-okaidia.css');

const StyledLink = styled(Link)`
  color: ${props => props.theme.colors.white.light};
  padding: 5px 10px;
  border: solid 1px #fff;
  border-radius: 20px;
  &:hover {
    color: ${props => props.theme.colors.black.blue};
    background: ${props => props.theme.colors.white.light};
  }
`;
const Header = styled.div`
display: flex;
justify-content:center;
text-align: center;
padding: 5% 0 2% 0;
//background: ${props => props.theme.colors.black.blue};
font-size: 2rem;
@media (max-width: 600px) {
  padding-top: 20%;
  padding-left: 5%;
  padding-right: 5%;
}
`;
const Information = styled.div`
  text-align: left;
  padding: 2% 10%;
  @media (max-width: 600px) {
    padding: 2% 5%;
  }
    .sideMenu {
    position: fixed; 
    color: #fff; 
    font-size: 14px; 
    font-weight: 600; 
    background: #0095ba; 
    padding: 10px 10px 10px 36px; 
    height: 100%; 
    width: 330px; 
    overflow: wrap; 
    top: 0; 
    left: 0; 
}
  .prism-code {
    padding: 5.75rem;
    border-radius: 5px;
    margin-bottom: 1rem;
    font-size: 16px;
  }
  h1 {
    font-size: 2rem;
    margin-bottom: 1.25rem;
  }
`;
const sideMenu = styled.div`
position: fixed; 
 color: #fff; 
 font-size: 14px; 
 font-weight: 600; 
 background: #0095ba; 
 padding: 10px 10px 10px 36px; 
 height: 100%; 
 width: 330px; 
 overflow: wrap; 
 top: 0; 
 left: 0; 
`;

const Doc = ({ data }) => {
  //const { posts, tagName } = pageContext;
  //const upperTag = tagName.charAt(0).toUpperCase() + tagName.slice(1);
  const post = data.markdownRemark;
  const postM = data.markdownRemark.html;
  return (
    <Layout>
      <Helmet title={`${post.frontmatter.title}  |  Limitless Unity Development`} />
      <Header style={{ background: post.backgroundCol }}>
        {post.frontmatter.title}
      </Header>
      <Container>
        <Information>
          
          <div dangerouslySetInnerHTML={{ __html: postM }} />
        </Information>
      </Container>
    </Layout>
  );
};

export default Doc;

Doc.propTypes = {
  pageContext: PropTypes.shape({

    //posts: PropTypes.array,
    //tagname: PropTypes.string,
  }),
  data: PropTypes.object.isRequired,
};

export const query = graphql`
query($pathSlug: String!) {
  markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
html
          frontmatter {
            path
            date
            title
            backgroundCol
          }
        }
      }
`;
